footer {
  color: rgba(250, 250, 250, .7);
  background-color: rgba(70, 79, 81, 1);
  background-image: -webkit-linear-gradient(45deg, rgba(70,79,81,1), rgba(250,250,250,.4), rgba(70,79,81,1));
  background-image: -o-linear-gradient(45deg, rgba(70,79,81,1), rgba(250,250,250,.4), rgba(70,79,81,1));
  background-image: linear-gradient(45deg, rgba(70,79,81,1), rgba(250,250,250,.4), rgba(70,79,81,1));
  padding: 10px;
}

iframe {
  width: 60%;
  height: auto;
}

@media (max-width: 414px) {
  iframe {
    width: 90%;
  }
}



