body {
  background-image: url('./../../images/oldpaperbackground.jpg');
}

.flip {
  -webkit-box-shadow: 12px 32px 12px 1px rgba(0,0,0,.2);
          box-shadow: 12px 32px 12px 1px rgba(0,0,0,.2);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
	-webkit-transition: all 1s ease;
	-o-transition: all 1s ease;
	transition: all 1s ease;
  margin: 7% 0;
}

.flip:hover {
	-webkit-transform: rotateY( 180deg );
	        transform: rotateY( 180deg );
}

.appLabels {
  margin-top: -15%;
	padding-bottom: 2%;
  position: relative;
  background-color: rgba(231,226,71,1);
}

#dwarf {
  width: 80%;
}



